
<template>
  <div>
    <Breadcrumb :value="common"></Breadcrumb>
    <el-card class="box-card">
      <div class="text item">
        <el-button type="success" style class="search_button"></el-button>
      </div>
      <!--  -->
      <el-alert
        class="alert"
        title=""
        type="warning"
        show-icon
        :closable="false"
      ></el-alert>

      <el-form ref="goodscateFormRef" label-width="110px">
        <!--  -->
        <el-form-item label="">
          <el-cascader
            style="width:20%"
            clearable
            v-model="selectedKeys"
            :options="options"
            :props="{ 
              expandTrigger: 'hover',
              value:'cat_id',
              label:'cat_name',
                children:'children'
               }"
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- tabs -->
        <el-tab-pane label="" name="first">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisablad"
            @click="addParamsVisible = true"
          ></el-button>
          <el-table :data="manytableData" style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <!--  -->
                  <el-tag
                    v-for="(tag,index) in props.row.attr_vals"
                    :key="index"
                    closable
                    type="danger"
                    class="el-tag"
                    @close="closeParams(props.row,tag)"
                  >{{tag}}</el-tag>
                  <!--  -->
                  <el-input
                    class="input-new-tag"
                    v-if="props.row.inputVisible"
                    v-model="addparamValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(props.row)"
                    @blur="handleInputConfirm(props.row)"
                  ></el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(props.row)"
                  >+ New Tag</el-button>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="" width="300px">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="editCateParamsName(scope.row)"
                ></el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="deleteCateParams(scope.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- tabs -->
        <el-tab-pane label="" name="second">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisablad"
            @click="addParamsVisible = true"
          ></el-button>
          <el-table :data="onlytableData" style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <!-- <el-form-item>
                    <span>{{ props.row.attr_vals }}</span>
                  </el-form-item>-->
                  <el-tag closable type="danger" class="el-tag">{{props.row.attr_vals}}</el-tag>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="" width="300px">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="addParamsVisible"
      width="45%"
      :before-close="handleClose"
    >
      <el-form
        :model="addParamsForm"
        status-icon
        :rules="paramsrules"
        ref="addParamsFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label="titleText" prop="paramsName">
          <el-input v-model="addParamsForm.paramsName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addParamsVisible = false"> </el-button>
        <el-button type="primary" @click="addCateParams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="editParamsVisible"
      width="45%"
      :before-close="handleClose"
    >
      <el-form
        :model="editParamsForm"
        status-icon
        :rules="paramsrules"
        ref="editParamsFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editParamsVisible = false"> </el-button>
        <el-button type="primary" @click="editCateParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getCategoriesList_api,
  getattributes_api,
  changeParams_api,
  addCateParams_api,
  deleteCateParams_api,
  
} from '@/api'
import {
 changeParamsCommon
} from '@/api/compute.js'
export default {
    data() {
        return {
            common: ['', ''], //
            //   
            //
            selectedKeys: [], //id
            //
            options: [], 
            activeName: 'first',//
            manytableData: [],
            //
            onlytableData: [],
            //tab
            sel: 'many',
            addparamValue: '', //value
            addParamsVisible: false, //
            editParamsVisible: false, //
            addParamsForm: {}, //  
            //
            editParamsForm: {
              attr_name: ''
            }, 
            paramsrules: {
                paramsName: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
                ],
                attr_name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
                ]
            }
        }
    },
    created() {
        this.getGoodsCateList()
    },
    computed: {
        isBtnDisablad() {
            if (this.selectedKeys.length !== 3) {
              return true
            }
            return false
        },
        titleText() {
            if (this.sel == 'many') {
              return ''
            } else if (this.sel == 'only') {
              return ''
            }
        }
    },
    methods: {
        //
        //
        //
        handleChange(value) {
            if (this.selectedKeys.length > 0) {
                this.getattributes()
            } else {
              //
                this.manytableData = []
                this.onlytableData = []
            }
        },
        //
        async getGoodsCateList() {
        const { data: res } = await getCategoriesList_api()
            //   console.log(res)
            if (res.meta.status != 200)
                return this.$message.error('')
            this.$message.success('')
            this.options = res.data
        },
        //tab
        // console.log(tab.name);
        handleClick(tab, event) {
            if (tab.name == 'first') {
                this.sel = 'many'
            } else if (tab.name == 'second') {
                this.sel = 'only'
            }
            // console.log(this.sel, 666)
            if (this.selectedKeys.length > 0) {
                //,
                // console.log(22)
                this.getattributes()
            }
        },
        //
        async getattributes() {
            //
            const { data: res } = await getattributes_api({
                id: this.selectedKeys[this.selectedKeys.length - 1],
                sel: this.sel
            })
            //   console.log(res.data)
            if (res.meta.status != 200)
                return this.$message.error('')
            this.$message.success('')
            //   console.log(res.data);
            res.data.forEach(item => {
            //input
                item.inputVisible = false
                item.attr_vals.length > 0 ? (item.attr_vals = item.attr_vals.split(',')) : (item.attr_vals = [])
          })
          this.sel == 'many'
            ? (this.manytableData = res.data)
            : (this.onlytableData = res.data)
        },
        // 
        async addCateParams() {
            const { data: res } = await addCateParams_api({
                attr_name: this.addParamsForm.paramsName,
                attr_sel: this.sel,
                id: this.selectedKeys[this.selectedKeys.length - 1]
            })
            //   console.log(res)
            if (res.meta.status !== 201)
                return this.$message.error('')
            this.$message.success('')
            this.getattributes()
            this.addParamsVisible = false
        },
        // 
        editCateParamsName(row) {
            //   console.log(row)
            this.editParamsVisible = true
            this.editParamsForm.attr_name = row.attr_name
            this.editParamsForm.attrId = row.attr_id
            this.editParamsForm.id = row.cat_id
            this.editParamsForm.attr_sel = row.attr_sel
            this.editParamsForm.attr_vals = row.attr_vals.join()
        },
        // 
        editCateParams() {
            //   console.log(this.editParamsForm)
            // if (this.editParamsForm.paramsName.trim()=='') return this.$message.error('')
            this.$refs.editParamsFormRef.validate(async result => {
                if (!result) return this.$message.error('')
                const { data: res } = await changeParams_api(this.editParamsForm)
                // console.log(res);
                if (res.meta.status !== 200)
                return this.$message.error('')
                this.$message.success('')
                this.editParamsVisible = false
                this.getattributes()
            })
        },
        // 
        async deleteCateParams(row) {
            // console.log(row)
            const confirmResult = await this.$confirm(
                ', ?',
                '',
                {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }
            ).catch(err => err)
            //confirmResult 'confirm'
            //, confirmResultcatch'cancel'
            if (confirmResult !== 'confirm') {
              return this.$message.info('')
            }
            //   console.log(row)

            const { data: res } = await deleteCateParams_api({
                id: row.cat_id,
                attrid: row.attr_id
            })
            if (res.meta.status !== 200)
                return this.$message.error('')
            this.$message.success('')
            this.getattributes()
        },
        // input
        showInput(rose) {
            //
            //   console.log(rose);
            // inut
            rose.inputVisible = true
            // input
            // $nextTickinput,inputv-if,
            this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus()
            })
        },
        // input
        async handleInputConfirm(rose) {
            if (this.addparamValue.trim().length == 0) {
                this.addparamValue = ''
                rose.inputVisible = false
                return
            }
            rose.attr_vals.push(this.addparamValue.trim())
            this.addparamValue = ''
            rose.inputVisible = false
            //   console.log(rose)
             // compute-api
            changeParamsCommon(rose).then(res => {
                // console.log(res)
                if (res.meta.status != 200) return this.$message.error('')
                this.$message.success('')
            })
        },
        async closeParams(row, tag) {
            const confirmResult = await this.$confirm(
              ', ?',
              '',
              {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }
            ).catch(err => err)
            //confirmResult 'confirm'
            //, confirmResultcatch'cancel'
            if (confirmResult !== 'confirm') {
                return this.$message.info('')
            }
            row.attr_vals.pop(tag)
            // 
            changeParamsCommon(row).then(res => {
                // console.log(res)
                if (res.meta.status !== 200)
                return this.$message.error('')
                this.$message.success('')
            })
        },
        handleClose(done) {
            this.addParamsVisible = false
            this.editParamsVisible = false
        },
      }
    }
</script>
<style lang='less' scoped>
.alert {
  margin: 10px 0;
}
.el-tag {
  margin: 10px;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin: 10px;
  vertical-align: bottom;
}
</style>