
<template>
  <div>
    <Breadcrumb :value="common"></Breadcrumb>
    <el-card class="box-card">
      <div class="text item">
        <el-button type="success" style class="search_button" @click="showCatedialogVisible"></el-button>
      </div>
      <tree-table
        index-text="#"
        :data="cateList"
        :columns="columns"
        :stripe="props.stripe"
        :border="props.border"
        :show-header="props.showHeader"
        :show-summary="props.showSummary"
        :show-row-hover="props.showRowHover"
        :show-index="props.showIndex"
        :tree-type="props.treeType"
        :is-fold="props.isFold"
        :expand-type="props.expandType"
        :selection-type="props.selectionType"
      >
        <!--  -->
        <template slot="valid" scope="scope">
          <i
            v-if="!scope.row.cat_deleted"
            class="el-icon-success"
            style="color:#85ce61;font-size:16px"
          ></i>
          <i v-else class="el-icon-error" style="color:red;font-size:16px"></i>
        </template>
        <!--  -->
        <template slot="sort" scope="scope">
          <el-tag v-if="scope.row.cat_level==0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level==1"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="operation" scope="scope">
          <!--  -->
          <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
          <!--  -->
          <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
        </template>
      </tree-table>
      <!-- // -->
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      @close="addCateClose"
      title=""
      :visible.sync="CatedialogVisible"
      width="40%"
      :before-close="handleClose"
    >
      <el-form ref="cateFormRef" :model="addCateForm" :rules="cateFormRules" label-width="100px">
        <!--  -->
        <el-form-item prop="cateName" label=":">
          <el-input
            v-model="addCateForm.cateName"
            placeholder=""
            prefix-icon="iconfont icon-user"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label=":">
          <el-cascader
            style="width:100%"
            clearable
            change-on-select
            v-model="selectedKeys"
            :options="options"
            :props="{ 
              expandTrigger: 'hover',
              value:'cat_id',
              label:'cat_name',
                children:'children'
               }"
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="CatedialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCateName"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
    import { getCategoriesList_api ,addCateName_api} from '@/api'
    export default {
        data() {
            return {
                CatedialogVisible: false, //
                addCateForm: {
                //value
                cateName: '',
                
                },
                // 
                cateFormRules: {
                    // 
                    cateName: [
                      { required: true, message: '', trigger: 'blur' },
                      { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
                    ]
                },
            
                common: ['', ''], //
                cateList: [], //
                queryInfo: {
                    //
                    pagenum: 1,
                    pagesize: 5
                },
                total: 0, //
                //   
                props: {
                    stripe: false,
                    border: true,
                    showHeader: true,
                    showSummary: false,
                    showRowHover: false,
                    showIndex: true,
                    treeType: true,
                    isFold: true,
                    expandType: false,
                    selectionType: false
                },
                //   
                columns: [
                    {
                      label: '',
                      prop: 'cat_name',
                      width: '300px'
                    },
                    {
                      label: '',
                      type: 'template',
                      template: 'valid'
                    },
                    {
                      label: '',
                      type: 'template',
                      template: 'sort'
                    },
                    {
                      label: '',
                      type: 'template',
                      template: 'operation'
                    }
                ],
                //   
                //
                selectedKeys:[],
                options: [],  //
                cateNameList:[]  //
            }
        },  
        created() {
            this.getCategoriesList()
        },  
        methods: {
            addCateClose() {  //
                // console.log(this.$refs.cateFormRef);
                this.$refs.cateFormRef.resetFields()  
                this.selectedKeys = [];
            },
            handleSizeChange(val) {
                //   console.log(val);
                this.queryInfo.pagesize = val
                this.getCategoriesList()
            },
            handleCurrentChange(val) {
                //   console.log(val);
                this.queryInfo.pagenum = val
                this.getCategoriesList()
            },
            async getCategoriesList() {
                const { data: res } = await getCategoriesList_api(this.queryInfo)
                if (res.meta.status != 200) return this.$message.error('')
                this.$message.success('')
                this.cateList = res.data.result
                this.total = res.data.total
                // console.log(res.data);
            },
            handleClose() {
                //
                this.CatedialogVisible = false
            },
            showCatedialogVisible() {
                //
                this.showGetCategoriesList()
                this.CatedialogVisible = true
            },
            handleChange(value) {  //
              //
                this.cateNameList=value
                //  console.log(value);
            },
            async showGetCategoriesList() {  //
                const { data: res } = await getCategoriesList_api({type:2})
                if (res.meta.status !== 200)
                  return this.$message.error('')
                this.$message.success('')
                // console.log(res.data);
                this.options=res.data
            },
            async addCateName(){  //
                if(this.addCateForm.cateName==''&&this.addCateForm.cateName<2){
                    return this.$message.error('')
                } 
                // console.log(this.cateNameList)
                // console.log(this.addCateForm.cateName)
                const params={} //
                params.cat_name=this.addCateForm.cateName
                if(this.cateNameList.length>0){    //or
                    params.cat_pid=this.cateNameList[this.cateNameList.length-1]
                    params.cat_level=this.cateNameList.length
                }else if(this.cateNameList.length<=0){
                    params.cat_pid=0
                    params.cat_level=0
                }
                // console.log(params);
                const {data:res}=await addCateName_api(params)
                // console.log(res);
                if (res.meta.status !== 201)
                return this.$message.error('')
                this.$message.success('')
                this.getCategoriesList()
                this.CatedialogVisible = false
            }
        }
    }
</script>
<style lang='less' scoped>
.search_button {
  margin-bottom: 10px;
}
</style>