<!--  -->
<template>
  <div>
    <Breadcrumb :value="common"></Breadcrumb>
    <el-card class="box-card">
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>
      <!--  -->
      <el-steps :active="activeIndex-0" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tab -->
      <el-form
        :model="addGoodsForm"
        status-icon
        :rules="checkAddForm"
        ref="addGoodsFormref"
        label-width="100px"
        class="demo-addGoodsForm"
        :label-position="labelPosition"
      >
        <el-tabs
          :tab-position="tabPosition"
          v-model="activeIndex"
          :before-leave="beforeTabLeave"
          :stretch="true"
          @tab-click="getCateParams"
        >
          <div style="margin: 20px;"></div>
          <!--  -->
          <!--  -->
          <el-tab-pane label="" name="0" class="from">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addGoodsForm.goods_name" autocomplete="off" clearable></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input
                type="number"
                v-model="addGoodsForm.goods_price"
                autocomplete="off"
                clearable
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input type="number" v-model="addGoodsForm.goods_weight" clearable></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input type="number" v-model="addGoodsForm.goods_number" clearable></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-cascader
                style="width:20%"
                clearable
                v-model="selectedKeys"
                :options="options"
                :props="{ 
                            expandTrigger: 'hover',
                            value:'cat_id',
                            label:'cat_name',
                              children:'children'
                             }"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item :label="item.attr_name" :key="item.attr_id" v-for="item in manyParams">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox border :label="val" v-for="(val,i) in item.attr_vals" :key="i"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="2">
            <!--  -->
            <el-form-item :label="item.attr_name" v-for="(item,i) in onlyParams" :key="i">
              <el-input v-model="item.attr_vals[0]"></el-input>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="3">
            <el-upload
              class="upload-demo"
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="uploadSuccess"
              :file-list="fileList"
              list-type="picture"
              :headers="headerObj"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addGoodsForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="btnAdd" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <!--  -->
      <el-dialog title="" :visible.sync="imgVisible" width="30%" >
        <img :src="previewPath" alt="" class="imgVisible">
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { getCategoriesList_api ,addGoods_api} from '@/api'
import { getCateParamsCommon } from '@/api/compute.js'
import { quillEditor } from 'vue-quill-editor'
//vue-quill-editor
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import _ from 'lodash'

export default {
  components: {
    quillEditor
  },
  data() {
    return {
      common: ['', ''], //
      activeIndex: 0, //
      tabPosition: 'left', //tab
      // value
      addGoodsForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        //
        pics: [],
        attrs: [],
        goods_introduce: ''
      },
      selectedKeys: [], //  
      options: [], //
      labelPosition: 'top',
      //   
      checkAddForm: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      //   
      goodsParams: {
        id: '',
        sel: ''
      },
      onlyParams: [], //
      manyParams: [], //
      fileList: [], //
      //headers
      headerObj: { Authorization: window.sessionStorage.getItem('token') },
      imgVisible:false,//
      previewPath:'',//
    }
  },
  methods: {
    //tab
    handleClick(tab, event) {},
    //tab
    beforeTabLeave(activeName, oldActiveName) {
      // 
      var res = true
      if (oldActiveName == '0') {
        this.$refs.addGoodsFormref.validate(result => {
        //   console.log(result, 666)
          if (!result) {
            res = false
          }
        })
        if (this.selectedKeys.length < 3) {
          res = false
          this.$message.error('')
        }
      }
      return res
    },
    // change
    handleChange() {
      this.addGoodsForm.goods_cat = this.selectedKeys
    },
    //
    async getGoodsCateList() {
      const { data: res } = await getCategoriesList_api()
      //   console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('')
      this.$message.success('')
      this.options = res.data
    },
    // /
    getCateParams() {
      // console.log(this.activeIndex);
      if (this.activeIndex == 1) {
        this.goodsParams.sel = 'many'
        this.goodsParams.id = this.selectedKeys[this.selectedKeys.length - 1]
      } else if (this.activeIndex == 2) {
        this.goodsParams.sel = 'only'
        this.goodsParams.id = this.selectedKeys[this.selectedKeys.length - 1]
      }
      if (this.activeIndex == 1 || this.activeIndex == 2) {
        getCateParamsCommon(this.goodsParams).then(res => {
          if (res.meta.status !== 200)
            return this.$message.error('')
          this.$message.success('')
          res.data.forEach(item => {
            item.attr_vals.length > 0
              ? (item.attr_vals = item.attr_vals.split(','))
              : (item.attr_vals = [])
          })
          this.goodsParams.sel == 'many'
            ? (this.manyParams = res.data)
            : (this.onlyParams = res.data)
        //   console.log(this.manyParams)
        //   console.log(this.onlyParams)
        })
      }
    },
    // 
    handleRemove(file) {
    //   console.log(file)
      const filePath = file.response.data.tmp_path
      const index = this.addGoodsForm.pics.findIndex(
        item => item.pic === filePath
      )
      //   
      this.addGoodsForm.pics.splice(index, 1)
    },
    // 
    handlePreview(file, fileList) {
        this.previewPath = file.response.data.url
        this.imgVisible=true;
    },
    // 
    uploadSuccess(response, file, fileList) {
      console.log(response, file, fileList)
      if (response.meta.status !== 200)
        return this.$message.error('')
      this.$message.success('')
      this.addGoodsForm.pics.push({ pic: response.data.tmp_path })
    },
    // 
    async add() {
        this.$refs.addGoodsFormref.validate(async valid=>{
            if(!valid) return this.$message.error('')
            const form=_.cloneDeep(this.addGoodsForm)
            form.goods_cat=form.goods_cat.join(',')
            // 
            // console.log(this.manyParams);
            this.manyParams.forEach(item=>{
                form.attrs.push({attr_id:item.attr_id,attr_value:item.attr_vals.join(" ")})
            })
            // 
            // console.log(this.onlyParams);
            this.onlyParams.forEach(item=>{
                form.attrs.push({attr_id:item.attr_id,attr_value:item.attr_vals[0]})
            })
            const{data:res}=await addGoods_api(form)
            if(res.meta.status!==201) return this.$message.error('')
            this.$message.success('')
            // 
            this.$router.push('/goods')
        });
    }
  },
  created() {
    this.getGoodsCateList()
  },
  computed: {}
}
</script>
<style lang='less' scoped>
.el-step {
  font-size: 13px;
}
.from .el-input {
  width: 100%;
}
.imgVisible {
    width: 100%;
}
</style>